export function testScreenType(size) {
  const windowWidth = window.innerWidth;

  switch (size) {
    case 'xs':
      return windowWidth < 576;
    case 'sm':
      return windowWidth >= 576;
    case 'md':
      return windowWidth >= 768;
    case 'lg':
      return windowWidth >= 992;
    case 'xl':
      return windowWidth >= 1200;
    case 'xxl':
      return windowWidth >= 1600;
    default:
      return false;
  }
}

export function formatNumber(value) {
  if (value === null || value === undefined || Number.isNaN(value)) {
    return 'NA';
  }

  return Number(Number(value).toPrecision(3)).toLocaleString();
}

export function tickFormatter(value) {
  return value.toLocaleString();
}

export function displayUnit(unit, perCapita) {
  return perCapita ? `${unit.base}/capita` : unit.main;
}

export function parseMapOfStatistics(mapOfCountryStatistics, defaultCountry) {
  return map(
    statistics =>
      typeof statistics === 'string'
        ? { statisticCode: statistics, countryCode: defaultCountry }
        : statistics,
    mapOfCountryStatistics,
  );
}

export function addPopCountryStatistics(mapOfCountryStatistics, perCapita) {
  if (!perCapita) {
    return mapOfCountryStatistics;
  }

  const countryCodes = uniq(
    values(mapOfCountryStatistics).map(d => d.countryCode),
  );
  const populations = mergeAll(
    countryCodes.map(countryCode => ({
      [`pop/${countryCode}`]: { statisticCode: 'POPULATION', countryCode },
    })),
  );
  return {
    ...populations,
    ...mapOfCountryStatistics,
  };
}

export function chartToPngBlob(domElement, scale) {
  const padding = 10;
  const height = domElement.offsetHeight + padding * 2;
  const width = domElement.offsetWidth + padding * 2;
  const classFiltered = [
    'ChartWrapper__header__right',
    'leaflet-control-container',
  ];
  domElement.classList.add('transformingToPng');
  return domToImage
    .toBlob(domElement, {
      filter: element => !classFiltered.includes(element.className),
      bgcolor: 'white',
      style: {
        padding: `${padding}px`,
        transform: `scale(${scale}) translate(${width / 2 / scale}px, ${height /
          2 /
          scale}px)`,
      },
      height: height * scale,
      width: width * scale,
    })
    .then(blob => {
      domElement.classList.remove('transformingToPng');
      return blob;
    });
}

export function memoize(
  fn,
  hashFunction = args => JSON.stringify(args),
  cacheSize = 30,
) {
  const cache = {};
  return function memoizedFun(...args) {
    const hash = hashFunction(args);
    if (cache[hash]) {
      return cache[hash];
    }
    const res = fn(...args);

    cache[hash] = res;

    const hashes = Object.keys(cache);
    if (hashes.length > cacheSize) {
      delete cache[hashes[0]];
    }

    return res;
  };
}

export function clickHandlers({ onClick, onDoubleClick }) {
  let clicks = 0;
  return function clickHandler(...args) {
    clicks += 1;
    if (clicks < 2) {
      setTimeout(() => {
        if (clicks < 2) {
          onClick(...args);
        } else {
          onDoubleClick(...args);
        }
        clicks = 0;
      }, 300);
    }
  };
}

export function maxAndIndex(array) {
  return array.reduce(
    (res, value, i) => {
      if (value > res.max) {
        return { max: value, maxIndex: i };
      }
      return res;
    },
    {
      max: 0,
      maxIndex: 0,
    },
  );
}
